html,
body {
  margin: auto;
	font-size: 12px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

canvas {
	margin: auto;
	top: 0; bottom: 0;
	left: 0; right: 0;
	position: absolute;
	border: 1px solid #000;
}

button {
	margin-top: 0;
	padding: 10px;
}

input {
	margin-left: 20px;
	padding: 8px;
}

div {
  /* pointer-events: none; */
	user-select: none;
}

.App {
	z-index: 1;
	margin: auto;
	width: 1200px;
	height: 550px;
	top: 0; bottom: 0;
	left: 0; right: 0;
	position: absolute;
	/* pointer-events: none; */
	/* background-color: red; */
}

.no-pointer-events {
	pointer-events: none;
}

.mouse-position-ui {
	color: #fff;
	top: 0;
	right: 0;
	position: absolute;
}

.weapon-mod-data {
  right: 0;
  color: #fff;
  position: absolute;
}

.mouse-state-ui {
	color: #fff;
	top: 14px;
	right: 0;
	position: absolute;
}

.body-data {
	color: #fff;
	padding: 4px;
	display: inline-block;
}

.debug-ui {
	color: #fff;
  pointer-events: none;
}

.total-ammo-count-ui {
	color: #fff;
	font-size: 14px;
	font-weight: bold;
	text-shadow: #000 1px 1px 1px;
	position: absolute;
	text-align: right;
	right: 24px;
	bottom: 92px;
}

.total-credits-ui {
  color: #fff;
	font-size: 13px;
	font-weight: bold;
	text-shadow: #000 2px 2px 2px;
	position: absolute;
	text-align: right;
	right: 20px;
	bottom: 2px;
}

.Toastify__toast--dark {
	font-size: 16px;
	font-weight: bold;
	text-shadow: rgba(0, 0, 0, 0.5) 2px 2px 2px;
}

.Toastify__toast--warning {
	font-size: 16px;
	font-weight: bold;
	/* color: #FF9600; */
	border: 2px solid #fff;
	text-shadow: rgba(0, 0, 0, 0.5) 2px 2px 2px;
}

.game-score {
	left: 10px;
	bottom: 10px;
	padding: 5px;
	padding-left: 10px;
	padding-right: 10px;
	border-radius: 3px;
	position: absolute;
	background-color: #222;
}

.game-score::before {
	content: 'game score: ';
}

.player-attributes {
  margin: auto;
  width: 480px;
  padding: 5px;
	padding-left: 10px;
	padding-right: 10px;
  position: absolute;
	/* background-color: rgba(0, 0, 0, 0.5); */
	background-color: rgba(33, 67, 40, 0.3);
  left: 0; right: 0; bottom: 10px;
  /* border-radius: 3px; */
  text-align: center;
}

/* .player-attributes div:first-of-type {
  margin-left: 5px;
} */

.player-attributes > div {
  display: inline-block;
  margin-right: 20px;
  text-align: center;
  padding: 1px 6px 1px 6px;
  border-radius: 1px;
}

.upgrade-gui {
	transform: scale(1.0);
	background-color: rgba(0, 0, 0, 0.35);
}

.upgrade-gui::before {
	content: '';
	position: absolute;
	display: block;
	left: -4px;
	/* margin-right: 20px; */
  text-align: center;
  /* padding: 1px 6px 1px 6px; */
	border-radius: 1px;
	width: 4px;
	height: 4px;
	background-color: seagreen;
}

.upgrade-gui-transition {
  color: darkgreen;
  background-color:#00FA9A;
  border: 1px solid darkgreen;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  transform: scale(1.15);
}

.weapon-stats {
  position: absolute;
  right: 145px;
  bottom: 20px;
  height: 74px;
  line-height: 19px;
  text-align: right;
  /* background-color: rgba(40, 110, 70, 0.3); */
  background-color: rgba(0, 0, 0, 0.25);
  padding: 8px;
}

#ragdolls-count {
	top: 0;
	right: 0;
	padding: 8px;
	position: absolute;
}
